  font-weight: normal !important;
  font-style: normal !important;
}
b, strong {
  font-family: "Colfax-Regular", Arial, "Helvetica Neue", Helvetica, sans-serif;
  font-weight: normal !important;
  font-style: normal;
}
/* Media Query Mixins */
@font-face {
  font-family: "Colfax-Medium";
  src: url("/static/fonts/Colfax/ColfaxWebMedium.woff") format("woff"), url("/static/fonts/Colfax/ColfaxWebMedium.woff2") format("woff2");
}
@font-face {
  font-family: "Colfax-Regular";
  src: url("/static/fonts/Colfax/ColfaxWebRegular.woff") format("woff");
}
@font-face {
  font-family: "Colfax-Light";
  src: url("/static/fonts/Colfax/ColfaxWebLight.woff") format("woff"), url("/static/fonts/Colfax/ColfaxWebLight.woff2") format("woff2");
}
html {
  font-size: 16px;
}
@media screen and (max-width: 767px) {
html {
      font-size: 14px;
}
}
body {
  font-size: 1em;
  line-height: 1.45;
  font-variant-caps: normal;
}
h1, .h1, h2, .h2, h3, .h3, h4, .h4 {
  margin: 1.414em 0 0.5em;
  line-height: 1.2;
}
h1, .h1 {
  margin-top: 0;
  font-size: 3.157em;
}
@media screen and (max-width: 767px) {
h1, .h1 {
      font-size: 2.441em;
}
}
h2, .h2 {
  font-size: 2.369em;
}
@media screen and (max-width: 767px) {
h2, .h2 {
      font-size: 1.953em;
}
}
h3, .h3 {
  font-size: 1.777em;
  margin-bottom: 1.414em;
}
@media screen and (max-width: 767px) {
h3, .h3 {
      font-size: 1.563em;
}
}
h4, .h4 {
  font-size: 1.333em;
}
@media screen and (max-width: 767px) {
h4, .h4 {
      font-size: 1.25em;
}
}
small, .font-small {
  font-size: .75em;
}
@media screen and (max-width: 767px) {
small, .font-small {
      font-size: .8em;
}
}
p {
  font-size: 1em;
}
p + p {
  margin-top: 1.3em;
}
a, a:visited {
  color: rgba(255, 255, 255, 0.5);
  text-transform: uppercase;
  -webkit-transition: color .3s ease;
  transition: color .3s ease;
}
a .lowercase, a:visited .lowercase {
    text-transform: capitalize;
}
a:hover, a:visited:hover {
    color: #fff;
    text-decoration: none;
}
.winner-explorer-modal {
  z-index: 19;
  font-size: 2rem;
  width: 500px;
}
.winner-explorer-modal .modal {
    -webkit-transform: none !important;
            transform: none !important;
}
.winner-explorer-modal .aeroglyph {
    text-align: center;
}
.winner-explorer-modal .message {
    font-size: 1rem;
    text-align: center;
    margin-bottom: 1rem;
    color: rgba(255, 255, 255, 0.8);
}
.winner-explorer-modal .message strong {
      color: #FFF;
}
.winner-explorer-modal .input-label {
    margin-top: 20px;
    font-size: .8rem;
    color: rgba(255, 255, 255, 0.5);
}
.winner-explorer-modal form {
    text-align: center;
}
.winner-explorer-modal form .name-selector-group {
      text-align: left;
      margin-bottom: 1rem;
}
.winner-explorer-modal form .name-selector-group label {
        margin-bottom: .2em;
}
.winner-explorer-modal .bottom-links {
    margin-top: 2rem;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -webkit-box-align: end;
        -ms-flex-align: end;
            align-items: flex-end;
    color: rgba(255, 255, 255, 0.2);
}
.winner-explorer-modal .bottom-links .share {
      -webkit-box-flex: 0;
          -ms-flex: 0 1 50%;
              flex: 0 1 50%;
}
.winner-explorer-modal .bottom-links .download {
      -webkit-box-flex: 0;
          -ms-flex: 0 1 20%;
              flex: 0 1 20%;
      text-align: center;
}
.winner-explorer-modal .bottom-links .download ul li {
        margin: 0 auto;
}
.winner-explorer-modal .bottom-links ul {
      margin-top: .5em;
}
.winner-explorer-modal .bottom-links > li {
      text-align: left;
}
.winner-explorer-modal .bottom-links .separator {
      -webkit-box-flex: 0;
          -ms-flex: 0 1 1px;
              flex: 0 1 1px;
      height: 24px;
      background-color: rgba(255, 255, 255, 0.2);
      margin-bottom: 5px;
}
@media screen and (max-height: 800px) {
.winner-explorer-modal .modal-header {
      padding-top: 1.5rem;
      padding-bottom: 1.5rem;
}
.winner-explorer-modal .modal-body {
      padding-top: .1rem;
}
.winner-explorer-modal .bottom-links {
      margin-top: 1.5rem;
}
}
